*{
  margin: 0px;
  padding: 0px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  background: white url(imagem/fundo.jpg) center center repeat ;
  background-size: cover;
  color: black;
}

.main{
  background-color: #ffffff;
  padding: 40px;
  border-radius: 8px;
  font-size: 1.7em;
  line-height: 50px;
}

h2{
  text-align: center;
  padding: 20px;
}

.botao{
  background-color: blue;
  color: white;
  border-radius: 5px;
  padding: 8px;
  margin-left: 50px;
}

.botao:hover{
  background-color: white;
  color: black;
}

input{
  height: 25px;
}

select{
  height: 25px;
}

.erro-message{
  max-width: 500px;
}